.form{   
    background: transparent;
    margin-top: 50px;
    padding: 10px 30px;
    border: 1px solid #fff;
    border-radius: 10px;
    .popup-container {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);

        display: flex;
        align-items: center;
        justify-content: center;
      }
      
      .popup {
        background: #fff;
        padding: 20px;
        border-radius: 5px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
        text-align: center;
        color: $link-color
      }
      
      .popup button {
        background: $linear-color;
        margin-top: 10px;
        padding: 8px 16px;
        cursor: pointer;
      }
      
    .formulaire{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        input{
            margin-top: 20px;
            width: 80%;
        }
        input[type=email]{
            background-color: rgba(228, 228, 225, 0.132);
            color: #fff;
            border: none;
            border-radius: 10px;
            outline: none;
            padding: 10px;
            width: 300px;
        }
        textarea{
            height: 100px;
            background-color: rgba(228, 228, 225, 0.132);
            border: none;
            outline: none;
            border-radius: 10px;
            color: #fff;
            padding: 10px;
            width: 300px;
            resize: none;
            margin-top: 20px;
        }
        input[type=submit]{
            outline: none;
            border: none;
            background: $linear-color;
            color: #fff;
            padding: 10px;
            width: 200px;
            border-radius: 10px;
            cursor: pointer;
            &:hover{
                filter: hue-rotate(25deg);
             }
        }
    }
    
}
@media screen and (max-width:500px) {
    .form{
        .popup{
            width: 90vw;
        }
    }
    
}